.pageSlider-enter{
  z-index:9999999;
  position: fixed;
}
.pageSlider-enter.left{
  transform: translate3d(-100%, 0, 0);
}

.pageSlider-enter.right {
  transform: translate3d(100%, 0, 0);
}

.pageSlider-enter.pageSlider-enter-active {
  transform: translate3d(0, 0, 0);
  transition: all 1000ms;
}